/* add css module styles here (optional) */

.test {
  margin: 2em;
  padding: 0.5em;
  border: 2px solid #000;
  font-size: 2em;
  text-align: center;
}

.a-picture {
  margin: auto;
  position: relative;
  min-width: 50px;
}
.a-picture__img {
  -o-object-fit: cover;
     object-fit: cover;
  border: none;
  position: relative;
  width: 100%;
  height: 100%;
}
.a-picture__loaded {
  background: transparent url("Spinning.gif") center/contain no-repeat;
  width: 100%;
  min-width: 50px;
  position: absolute;
  height: 100%;
  bottom: 0;
  display: flex;
  justify-content: center;
  align-items: center;
}
.a-picture__icon {
  display: none;
  position: fixed;
  align-items: center;
  justify-content: center;
  top: 10px;
  right: 20px;
  border-radius: 50%;
  height: 50px;
  width: 50px;
  margin: 0;
  overflow: hidden;
  z-index: 100;
  font-size: 20px;
  color: #fff;
}
.a-picture__icon:hover {
  cursor: pointer;
}
@media screen and (min-width: 708px) {
  .a-picture__icon {
    display: flex;
  }
}

.hidden {
  height: 0px;
}